
.align-center {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.field-label {
  padding-top: 0 !important;
}
header {
  margin: 1.5em auto;
  text-align: center;
}
.direction input[type = "radio"] {
  display: none;
}
.direction .field {
  position: relative;
  width: 100%;
  height: 70px;
}
.direction .field .centerFill {
  position: absolute;
  top: 12px;
  left: 12px;
  width: 38px;
  height: 38px;
  background: #fff;
}
.direction .field .not-rotate {
  width: 60px;
  height: 60px;
}
.direction .field .not-rotate label {
  width: 30px;
  height: 30px;
  background: #194d33;
}
.direction .field .rotate {
  width: 54px;
  height: 54px;
  margin: 4px 0 0 4px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.direction .field .rotate label {
  width: 27px;
  height: 27px;
  background: #3fc180;
}
.direction .field .control {
  position: absolute;
  top: 0;
  left: 0;
}
.direction .field .control label {
  position: absolute;
  margin-left: 0;
  transition: all 0.4s ease;
}
.direction .field .control label:hover {
  background: #e96900;
}
.direction .field .control label.top,
.direction .field .control label.left-top {
  top: 0;
  left: 0;
}
.direction .field .control label.right,
.direction .field .control label.right-top {
  top: 0;
  right: 0;
}
.direction .field .control label.bottom,
.direction .field .control label.right-bottom {
  bottom: 0;
  right: 0;
}
.direction .field .control label.left,
.direction .field .control label.left-bottom {
  left: 0;
  bottom: 0;
}
.color-picker {
  display: inline-block;
}
.workArea .triangleOutput {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  min-height: 15em;
  overflow: auto;
  background: #fff;
  background-image: linear-gradient(45deg, #bbb 25%, transparent 0), linear-gradient(45deg, transparent 75%, #bbb 0), linear-gradient(45deg, #bbb 25%, transparent 0), linear-gradient(45deg, transparent 75%, #bbb 0);
  background-position: 0 0, 15px 15px, 15px 15px, 30px 30px;
  background-size: 30px 30px;
}
.workArea .triangleOutput .triangle {
  margin: 0 auto;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 100px;
  transition: all 0.4s ease;
}
.workArea .cssOutput {
  margin-top: 1.5em;
}
.workArea .cssOutput pre {
  padding: 0.2em 1.4em;
  font-family: Consolas;
  background: #d3d3d3;
  overflow-x: auto;
}


